import { useState } from "react";
import { useRouter } from "next/router";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import { LineChart, Bot, Rocket } from "lucide-react";

const OnboardingPage = () => {
  const router = useRouter();
  const [step, setStep] = useState(1);
  const totalSteps = 3;

  const handleNext = () => {
    if (step < totalSteps) {
      setStep(step + 1);
    } else {
      router.push("/dashboard");
    }
  };

  const renderStepIcon = (currentStep: number) => {
    switch (currentStep) {
      case 1:
        return <Rocket className="w-12 h-12 text-blue-400" />;
      case 2:
        return <LineChart className="w-12 h-12 text-blue-400" />;
      case 3:
        return <Bot className="w-12 h-12 text-blue-400" />;
      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 flex flex-col items-center justify-center p-6">
      <div className="w-full max-w-2xl space-y-8">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-white mb-2">Scope Labs</h1>
          <p className="text-gray-400">AI-Powered Digital Competition Analysis</p>
        </div>

        <Progress 
          value={(step / totalSteps) * 100} 
          className="w-full h-2 bg-gray-700"
        />

        <div className="bg-gray-800/50 backdrop-blur-sm p-8 rounded-lg space-y-8 border border-gray-700">
          <div className="flex justify-center mb-6">
            {renderStepIcon(step)}
          </div>

          {step === 1 && (
            <div className="space-y-6">
              <h2 className="text-2xl font-bold text-white text-center">Welcome to Scope Labs</h2>
              <p className="text-gray-400 text-center">
                Get ready to transform your digital strategy with AI-powered competitor analysis and automation.
              </p>
              <div className="grid grid-cols-2 gap-4 mt-6">
                <div className="bg-gray-900/50 p-4 rounded-lg">
                  <h3 className="text-white font-semibold mb-2">Real-time Analysis</h3>
                  <p className="text-gray-400 text-sm">Track competitor movements as they happen</p>
                </div>
                <div className="bg-gray-900/50 p-4 rounded-lg">
                  <h3 className="text-white font-semibold mb-2">AI Automation</h3>
                  <p className="text-gray-400 text-sm">Implement optimizations automatically</p>
                </div>
              </div>
            </div>
          )}

          {step === 2 && (
            <div className="space-y-6">
              <h2 className="text-2xl font-bold text-white text-center">Customize Your Intelligence</h2>
              <p className="text-gray-400 text-center">
                Configure your competitive analysis dashboard for maximum insight.
              </p>
              <div className="grid grid-cols-1 gap-4 mt-6">
                <div className="bg-gray-900/50 p-4 rounded-lg">
                  <h3 className="text-white font-semibold mb-2">Select Competitors</h3>
                  <p className="text-gray-400 text-sm">Choose up to 5 main competitors to track</p>
                </div>
                <div className="bg-gray-900/50 p-4 rounded-lg">
                  <h3 className="text-white font-semibold mb-2">Set KPIs</h3>
                  <p className="text-gray-400 text-sm">Define your key performance indicators</p>
                </div>
              </div>
            </div>
          )}

          {step === 3 && (
            <div className="space-y-6">
              <h2 className="text-2xl font-bold text-white text-center">Ready for Launch</h2>
              <p className="text-gray-400 text-center">
                Your AI-powered competitive advantage is about to begin.
              </p>
              <div className="bg-gray-900/50 p-4 rounded-lg">
                <h3 className="text-white font-semibold mb-2">Your Setup is Complete</h3>
                <p className="text-gray-400 text-sm">
                  Access your personalized dashboard to start tracking and implementing optimizations
                </p>
              </div>
            </div>
          )}

          <Button
            onClick={handleNext}
            className="w-full bg-blue-600 hover:bg-blue-700 text-white py-6 rounded-lg transition-all duration-200 ease-in-out"
          >
            {step === totalSteps ? "Launch Dashboard" : "Continue"}
          </Button>
        </div>

        <div className="text-center text-gray-500 text-sm">
          Step {step} of {totalSteps}
        </div>
      </div>
    </div>
  );
};

export default OnboardingPage;